$bg-color: #141414;
body{
    background-color: $bg-color;
}

.containerCards {
    display: flex;
    justify-content: center;
    width: 100%;
    padding: 0 4%;

    .panelCards {
        box-shadow: 0 5px 1rem rgba(0, 0, 0, 0.1);
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        height: 80vh;
        border-radius: 30px;
        color: #fff;
        cursor: pointer;
        flex: 0.5;
        margin: 10px;
        position: relative;
        transition: flex 0.5s ease-in;
        
        &:nth-child(1){
            background-image: url('../../img/projets.png')
        }
        &:nth-child(2){
            background-image: url('../../img/experiences.png')
        }
        &:nth-child(3){
            background-image: url('../../img/etudes.png')
        }
        &:nth-child(4){
            background-image: url('../../img/informations.png')
        }
        &:nth-child(5){
            background-image: url('../../img/contact.png')
        }

        h3 {
            font-size: calc(24 / 16 * 1rem);
            position: absolute;
            bottom: 20px;
            left: 20px;
            margin: 0;
            opacity: 0;
            text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
        }
    }
    
}




.panelCards.active {
    flex: 5;

    h3 {
        opacity: 1;
        transition: all 0.5s ease-in 0.4s;
    }
}



@media (max-width: 480px) {
    .containerCards {
        width: 100vw;

    }

    
}